import React, {useEffect} from "react";
import {gsap, Elastic} from "gsap";
import {ScrollTrigger} from "gsap/ScrollTrigger";

const Portfolio = ({onOpenProjects}) => {

    gsap.registerPlugin(ScrollTrigger)

    useEffect(() => {

            gsap.to("#title2", {
                x: 30,
                scrollTrigger: {
                    trigger: "#title2",
                    scrub: 1,
                    start: 'top 70%',
                    end: 'top 15%',
                },
            })


            gsap.fromTo(".portfolio_button", {
                    scale: 0.5,
                },
                {
                    scale: 1,
                    duration: 10,
                    scrollTrigger: {
                        trigger: ".portfolio_button",
                        scrub: 1,
                        start: 'top 95%',
                        end: 'top 80%',
                    },
                }
            )

            ScrollTrigger.matchMedia({
                "(min-width:1441px)": function () {
                    gsap.fromTo(".two", {
                            x: -280,
                            opacity: 0.2,
                        },
                        {
                            x: 0,
                            duration: 10,
                            opacity: 1,
                            scrollTrigger: {
                                trigger: ".two",
                                scrub: 1,
                                start: 'top 75%',
                                end: 'top 50%',
                            },
                        }
                    )
                }
            })
            ScrollTrigger.matchMedia({
                "(min-width:1441px)": function () {
                    gsap.fromTo(".three", {
                            x: -530,
                            opacity: 0.1,
                        },
                        {
                            x: 0,
                            duration: 10,
                            opacity: 1,
                            scrollTrigger: {
                                trigger: ".three",
                                scrub: 1,
                                start: 'top 75%',
                                end: 'top 50%',
                            },
                        })
                }
            })
            ScrollTrigger.matchMedia({
                "(min-width:1441px)": function () {
                    gsap.fromTo(".four", {
                            x: 450,
                            opacity: 0.1,
                        },
                        {
                            x: 0,
                            duration: 10,
                            opacity: 1,
                            scrollTrigger: {
                                trigger: ".four",
                                scrub: 1,
                                start: 'top 75%',
                                end: 'top 50%',
                            },
                        })
                }
            })
            ScrollTrigger.matchMedia({
                "(min-width:1441px)": function () {
                    gsap.fromTo(".five", {
                            x: 225,
                            opacity: 0.2,
                        },
                        {
                            x: 0,
                            duration: 10,
                            opacity: 1,
                            scrollTrigger: {
                                trigger: ".five",
                                scrub: 1,
                                start: 'top 75%',
                                end: 'top 50%',
                            },
                        })
                }
            })

            ScrollTrigger.matchMedia({
                "(min-width:1025px)": function () {
                    gsap.fromTo(".two", {
                            x: -280,
                            opacity: 0.2,
                        },
                        {
                            x: 0,
                            duration: 10,
                            opacity: 1,
                            scrollTrigger: {
                                trigger: ".two",
                                scrub: 1,
                                start: 'top 90%',
                                end: 'top 50%',
                            },
                        }
                    )
                }
            })
            ScrollTrigger.matchMedia({
                "(min-width:1025px)": function () {
                    gsap.fromTo(".three", {
                            x: -530,
                            opacity: 0.1,
                        },
                        {
                            x: 0,
                            duration: 10,
                            opacity: 1,
                            scrollTrigger: {
                                trigger: ".three",
                                scrub: 1,
                                start: 'top 90%',
                                end: 'top 50%',
                            },
                        })
                }
            })
            ScrollTrigger.matchMedia({
                "(min-width:1025px)": function () {
                    gsap.fromTo(".four", {
                            x: 420,
                            opacity: 0.1,
                        },
                        {
                            x: 0,
                            duration: 10,
                            opacity: 1,
                            scrollTrigger: {
                                trigger: ".four",
                                scrub: 1,
                                start: 'top 90%',
                                end: 'top 50%',
                            },
                        })
                }
            })
            ScrollTrigger.matchMedia({
                "(min-width:1025px)": function () {
                    gsap.fromTo(".five", {
                            x: 210,
                            opacity: 0.2,
                        },
                        {
                            x: 0,
                            duration: 10,
                            opacity: 1,
                            scrollTrigger: {
                                trigger: ".five",
                                scrub: 1,
                                start: 'top 90%',
                                end: 'top 50%',
                            },
                        })
                }
            })

            ScrollTrigger.matchMedia({
                "(max-width:1024px)": function () {
                    gsap.fromTo(".one", {
                            x: '-100%',
                            opacity: 0
                        },
                        {
                            x: 0,
                            opacity: 1,
                            scrollTrigger: {
                                trigger: ".one",
                                scrub: 1,
                                start: 'top 70%',
                                end: 'top 50%',
                            },
                        })
                }
            })
            ScrollTrigger.matchMedia({
                "(max-width:1024px)": function () {
                    gsap.fromTo(".two", {
                            x: '-100%',
                            opacity: 0
                        },
                        {
                            x: 0,
                            opacity: 1,
                            scrollTrigger: {
                                trigger: ".two",
                                scrub: 1,
                                start: 'top 70%',
                                end: 'top 50%',
                            },
                        })
                }
            })
            ScrollTrigger.matchMedia({
                "(max-width:1024px)": function () {
                    gsap.fromTo(".three", {
                            x: '-100%',
                            opacity: 0
                        },
                        {
                            x: 0,
                            opacity: 1,
                            duration: 10,
                            scrollTrigger: {
                                trigger: ".three",
                                scrub: 1,
                                start: 'top 79%',
                                end: 'top 50%',
                            },
                        })
                }
            })
            ScrollTrigger.matchMedia({
                "(max-width:1024px)": function () {
                    gsap.fromTo(".four", {
                            x: '-100%',
                            opacity: 0
                        },
                        {
                            x: 0,
                            opacity: 1,
                            duration: 10,
                            scrollTrigger: {
                                trigger: ".four",
                                scrub: 1,
                                start: 'top 79%',
                                end: 'top 50%',
                            },
                        })
                }
            })
            ScrollTrigger.matchMedia({
                "(max-width:1024px)": function () {
                    gsap.fromTo(".five", {
                            x: '-100%',
                            opacity: 0
                        },
                        {
                            x: 0,
                            opacity: 1,
                            duration: 10,
                            scrollTrigger: {
                                trigger: ".five",
                                scrub: 1,
                                start: 'top 79%',
                                end: 'top 50%',
                            },
                        })
                }
            })
            ScrollTrigger.matchMedia({
                "(max-width:1024px)": function () {
                    gsap.fromTo(".six", {
                            x: '-100%',
                            opacity: 0
                        },
                        {
                            x: 0,
                            opacity: 1,
                            duration: 10,
                            scrollTrigger: {
                                trigger: ".six",
                                scrub: 1,
                                start: 'top 79%',
                                end: 'top 50%',
                            },
                        })
                }
            })
        },
        []
    )

    return (
        <div id="portfolio" className="portfolio_block">
            <div className="portfolio_placeholder">
                <h1 id={"title2"} className="title">
                    portfolio
                </h1>
            </div>
            <div className="portfolio_projects_block">
                <div className="top_section">
                    <div className="portfolio_item one" onClick={() => window.open('https://www.firalabs.com/')}>
                        <span className="portfolio_description">Fira Labs DAO enables digital product professionals to have ownership in the organization and maximize earnings.</span>
                    </div>
                    <div className="portfolio_item two" onClick={() => window.open('https://inkin.com/')}>
                        <span className="portfolio_description">inKin Wellness is a global SaaS platform that helps people connect, get active and wholesome.</span>
                    </div>
                    <div className="portfolio_item three" onClick={() => window.open('https://etrooperz.com')}>
                        <span className="portfolio_description">eTrooperz is a fast-growing marketing agency providing a wide range of services to companies worldwide.</span>
                    </div>
                </div>
                <div className="bottom_section">
                    <div className="portfolio_item four">
                        <span className="portfolio_description">ArtinArm LLC is the distributor and manufacturer of unique stuff that solves a problem and puts a smile on your face.</span>
                    </div>
                    <div className="portfolio_item five" onClick={() => window.open('https://iabsis.com')}>
                        <span className="portfolio_description">Iabsis is specialized in creating competitive advantage for its clients using technological innovation mainly in the air transportation, and healthcare industries.</span>
                    </div>
                    <div className="portfolio_item six" onClick={() => window.open('https://blockem.io')}>
                        <span className="portfolio_description">Blockem is the most advanced anti-scam detection software on the market, analysing more than a billion Ethereum transactions per second.</span>
                    </div>
                </div>
            </div>

        </div>
    )
}
export default Portfolio
