import image from '../../Images/photo_rob.jpg';
import React, {useEffect} from "react";
import { gsap } from "gsap";
import {ScrollTrigger} from "gsap/ScrollTrigger";

const About = () => {
    gsap.registerPlugin(ScrollTrigger)

    useEffect(()=>{
      gsap.to("#title1",{
            x:-80,
            scrollTrigger: {
                trigger: "#title1",
                scrub: 1,
                start: 'top 60%',
                end: 'top 40%',
            }
        });
        gsap.to(".liner",{
            rotation :0 ,
            duration: 10,
            scrollTrigger: {
                trigger: ".liner",
                scrub: true,
                start: 'top 75%',
                end: 'top 20%',
            }
        })
        gsap.to(".image_block",{
            rotation :0,
            duration: 10,
            scrollTrigger: {
                trigger: ".image_block",
                scrub: true,
                start: 'top 75%',
                end: 'top 20%',
            }
        })
        gsap.to(".text",{
            y:-120,
            duration: 10,
            scrollTrigger: {
                trigger: ".text",
                scrub: true,
                start: 'top 90%',
                end: 'top 20%',
            },
        })
    },[])

    return(
        <div id="about" className="about_block">
            <div className="about_placeholder">
                <h1 id={'title1'} className="title">about me</h1>
            </div>
            <div className="about_body">
                <div className="liner">
                    <figure className="image_block">
                        <img src={image} alt="Img" className="image" />
                    </figure>
                </div>
                <div className="about_info">
                    <span className="text">
                       My professional loves are in Tech for Good  Web3  Social Impact Business  Healthcare Tech.
                    </span>
                    <span className="text">
                        Im an entrepreneur and business executive. I love building people and businesses.
                    </span>
                    <span className="text">
                        Lets discover how we may be able to collaborate and mastermind together!
                    </span>
                </div>
            </div>
        </div>
    )
}
export default About