import './App.css';
import './media.css'
import React, {useCallback, useState} from "react";
import Header from "./Component/Header/header";
import Person from "./Component/Persone/person";
import About from "./Component/About/about";
import Portfolio from "./Component/Portfolio/portfolio";
import Experience from "./Component/Experience/experience";
import Menu from "./Component/Menu/menu";
import data from './data.json'
import InfiniteScrollSlide from "./Component/InfiniteScroll/infiniteScroll";
import Contact from "./Component/Contact/contact";

function App() {
    const [isOpen, setIsOpen] = useState(false)
    const [view,setView]= useState(false)

    const toggleMenu = useCallback((show) => {
        setIsOpen(show)
    }, [])

    const onOpenProjects =() =>{
        setView(!view)
    }

    return (

        <div>

            <Header
                name={data.r.name}
                surname={data.r.surname}
                onOpenMenu={() => toggleMenu(true)}
            />

            <Person
                name={data.r.name}
                surname={data.r.surname}
                profession={data.r.profession}
            />

            <About/>
            <Portfolio
                onOpenProjects={onOpenProjects}
            />
            <Experience/>
            <Menu
                isOpen={isOpen}
                onClose={() => toggleMenu(false)}
            />
            <InfiniteScrollSlide/>
            <Contact/>
        </div>
    );
}

export default App;
